
import { default as Web3 } from 'web3'
import { default as contract } from 'truffle-contract'
import ccjson from  '../../build/contracts/CloudCompute.json'
import { resolve } from 'any-promise';

const cc = contract(ccjson)
let accounts
let account
let ipaddr

const App={
    init:async ()=>{
        cc.setProvider(web3.currentProvider)
        web3.eth.getAccounts(function (err, accs) {
            if (err != null) {
              alert('There was an error fetching your accounts.')
              return
            }
      
            if (accs.length === 0) {
              alert("Couldn't get any accounts! Make sure your Ethereum client is configured correctly.")
              return
            }
            accounts = accs
            account = accounts[0]
        })
        App.bindEvents();
        return await App.initWeb3();
    },

    updateList:async ()=>{
        var ccinstance;
        let cclen;
        
        cc.deployed().then((instance)=>{
            ccinstance=instance;
            //return ccinstance.getAllvmLen.call( { from: account });
            return ccinstance.getAllvmLen({ from: account });
        }).then((value)=>{
            console.log(value.toNumber());
            return value.c[0];
        }).then((value)=>{
            $('#resultList').empty();
            for (let index = 0; index < value; index++) {
                var local=new Promise((resolve,reject)=>{
                    setTimeout(()=>{
                        resolve(ccinstance.getVm(index,{ from: account }));
                    },50);
                });
                local.then((value)=>{
                    $('#resultList').append($('<li>').text(
                        value[0]+' '+
                        value[1].toNumber()+' '+
                        value[2].toNumber()+' '+
                        value[3].toNumber()+' '+
                        value[4].toNumber()+' '+
                        value[5].toNumber()));
                });
                //let vm=ccinstance.getVm(index,{ from: account });
                
        }
        }).catch((err)=>{
            console.log(err.message);
        });
        
    },


    initWeb3:async ()=>{
        
        return App.updateList();
    },

    
    handleDeploy:()=>{
        console.log('click');
        
        var ccinstance;
        web3.eth.getAccounts((error,accounts)=>{
            if(error)
            {
                console.log(error);
            }
            else
            {
                console.log(accounts);
            }
            cc.deployed().then((instance)=>{
                ccinstance=instance;
                return ccinstance.createInstance(
                    parseInt($('#vmtype').val()),
                    $('#vmName').val(),
                    parseInt($('#cpucore').val()),
                    parseInt($('#memSize').val()),
                    parseInt($('#vmemSize').val()),
                    parseInt($('#vpower').val()),
                    { from: account });
            }).then((status)=>{
                if(status)
                {
                    App.updateList();
                } 
            }).catch((err)=>{
                console.log(err.message);
            });
        })
    },

    bindEvents:async ()=>{
        //$(document).on('click','#deploycc',App.handleDeploy);
        $('#vm').submit((e)=>{
            App.handleDeploy();
            e.preventDefault();
        });

        $(document).on('click','#test',()=>{
            $('#vmtype').val();
        })
        

    }
}

window.App = App

window.addEventListener('load', function () {
    // Checking if Web3 has been injected by the browser (Mist/MetaMask)
    if (typeof web3 !== 'undefined') {
      // Use Mist/MetaMask's provider
      window.web3 = new Web3(web3.currentProvider)
    } else {
      console.warn(
        'No web3 detected. Falling back to http://127.0.0.1:9545.' +
        ' You should remove this fallback when you deploy live, as it\'s inherently insecure.' +
        ' Consider switching to Metamask for development.' +
        ' More info here: http://truffleframework.com/tutorials/truffle-and-metamask'
      )
      // fallback - use your fallback strategy (local node / hosted node + in-dapp id mgmt / fail)
      window.web3 = new Web3(new Web3.providers.HttpProvider('http://127.0.0.1:9545'))
    }
    $(document).ready(()=>{
        $.get('http://jsonip.com', (res) =>{
            console.log(res.ip);
            ipaddr=res.ip;
        });
    });
        
    App.init()
  })
  